<template>
  <div class="basic-info inner-page">
    <el-form
      ref="basicInfo"
      :model="basicInfo"
      label-width="120px"
      label-position="left"
    >
      <h1>{{ traduire("basic") }}</h1>
      <el-form-item :label="traduire('trainName')">
        <el-input
          v-model="basicInfo.lineName"
          maxlength="3"
          show-word-limit
        ></el-input>
      </el-form-item>
      <el-form-item :label="traduire('position')">
        <el-input-number
          v-model="basicInfo.pos"
          :min="0"
          :max="10"
          :label="traduire('position')"
        ></el-input-number>
      </el-form-item>
      <el-form-item :label="traduire('notification')">
        <el-input
          type="textarea"
          :rows="3"
          v-model="basicInfo.updateDate"
          placeholder="时刻表更新于：后面的内容"
        ></el-input>
      </el-form-item>
      <!-- <el-form-item label="车次位数">
        <el-input-number v-model="num" :min="3" :max="4"></el-input-number>
      </el-form-item> -->
    </el-form>
    <el-button class="normal-btn next" type="primary" @click="onSubmit">{{
      traduire("next")
    }}</el-button>
  </div>
</template>

<script>
  import zhCn from "@/assets/i18n/zh-cn";
  export default {
    name: "BasicVue",
    props: ["config", "locale"],
    watch: {
      config() {
        this.basicInfo = this.config;
      },
    },
    data() {
      return {
        num: 3,
        curFile: "",
        basicInfo: {
          lingName: "",
          pos: 1,
          updateDate: "",
        },
      };
    },

    methods: {
      traduire(name) {
        return this.locale[name] !== undefined ? this.locale[name] : zhCn[name];
      },
      onSubmit() {
        this.$emit("submit", this.basicInfo);
      },
    },
  };
</script>

<style lang="scss" scoped>
  .basic-info {
    width: 50%;
    min-width: 400px;
    height: 400px;
  }
</style>
