<template>
  <div class="inner-page">
    <h1>{{ title }}</h1>
    <el-table :data="tableData" style="width: 100%">
      <el-table-column
        header-align="center"
        align="center"
        :label="traduire('action')"
        #default="scope"
      >
        <el-button
          type="primary"
          @click="editTrain(scope.row)"
          class="normal-btn"
          >{{ traduire("edit") }}</el-button
        >

        <el-popconfirm
          :title="`${traduire('deleteTip')} ${scope.row.trainNum} ？`"
          @confirm="deleteTrain(scope.row)"
          icon-color="red"
        >
          <template #reference>
            <el-button type="danger">{{ traduire("delete") }}</el-button>
          </template>
        </el-popconfirm>
      </el-table-column>
      <el-table-column
        header-align="center"
        align="center"
        :label="traduire('type')"
        #default="scope"
      >
        <div class="type-column" v-if="scope.row.type.length > 0">
          <img
            class="type-icon"
            v-for="t in types"
            :key="t"
            :src="require(`../assets/img/${t}.png`)"
            :alt="t"
            :style="
              scope.row.type.indexOf(t) > -1 ? 'display:block' : 'display:none'
            "
          />
        </div>
        <div v-else class="type-column">无</div>
      </el-table-column>
      <el-table-column
        header-align="center"
        align="center"
        prop="trainNum"
        :label="traduire('trainNum')"
      >
      </el-table-column>
      <el-table-column
        v-for="(s, index) in stations"
        :key="index"
        header-align="center"
        align="center"
        #default="scope"
        :label="s"
      >
        {{ scope.row.stops[s] }}
      </el-table-column>
    </el-table>
    <el-button
      type="primary"
      @click="gotoAddTrain"
      style="margin-top: 16px"
      class="normal-btn"
      >{{ traduire("addTrain") }}</el-button
    >
  </div>

  <!-- 添加车次弹窗 -->
  <el-dialog
    custom-class="add-train-dialog"
    v-model="addTrainDialog"
    :title="locale.editTrain"
  >
    <el-form :model="initTrain" label-width="120px" label-position="left">
      <el-form-item :label="traduire('trainNum')">
        <el-input
          v-model="initTrain.trainNum"
          :placeholder="traduire('trainNum')"
          style="width: 200px"
        ></el-input>
      </el-form-item>
      <el-form-item :label="traduire('type')">
        <el-checkbox-group v-model="initTrain.type">
          <el-checkbox-button v-for="t in types" :key="t" :label="t">
            {{ traduire(t) }}
          </el-checkbox-button>
        </el-checkbox-group>
      </el-form-item>
      <el-scrollbar class="edit-time-part">
        <div class="time-box" v-for="(s, index) in stations" :key="index">
          <div>{{ s }}</div>
          <el-input
            v-model="initTrain.stops[s]"
            :placeholder="traduire('time')"
          ></el-input>
        </div>
      </el-scrollbar>
    </el-form>

    <template #footer>
      <span class="dialog-footer">
        <el-button
          type="primary"
          @click="addTrainDialog = false"
          class="normal-btn"
          >{{ locale.cancel }}</el-button
        >
        <el-button type="primary" @click="addTrain" class="normal-btn">{{
          locale.save
        }}</el-button>
      </span>
    </template>
  </el-dialog>
</template>
<script>
  import { ElNotification } from "element-plus";
  import zhCn from "@/assets/i18n/zh-cn";
  export default {
    name: "TimeTable",
    props: ["tableInit", "title", "stations", "locale"],
    data() {
      return {
        types: ["Full", "Temp", "Every", "Sub"],

        initTrain: {
          trainNum: "",
          stops: {},
          type: [],
        },
        tableData: [],
        addTrainDialog: false,
      };
    },
    watch: {
      tableInit() {
        this.tableData = this.tableInit;
      },
    },

    mounted: function () {
      this.tableData = this.tableInit;
    },
    methods: {
      traduire(name) {
        return this.locale[name] !== undefined ? this.locale[name] : zhCn[name];
      },
      gotoAddTrain() {
        this.stations.forEach((s) => {
          this.initTrain.stops[s] = "";
        });
        this.addTrainDialog = true;
      },
      editTrain(train) {
        this.initTrain = JSON.parse(JSON.stringify(train));
        this.addTrainDialog = true;
      },
      deleteTrain(train) {
        this.tableData = this.tableData.filter(
          (data) => data.trainNum !== train.trainNum
        );
      },
      addTrain() {
        let idx = this.tableData.findIndex(
          (train) => train.trainNum === this.initTrain.trainNum
        );
        if (idx < 0) {
          this.tableData.push(JSON.parse(JSON.stringify(this.initTrain)));
          this.addTrainDialog = false;
          ElNotification({
            title: this.traduire("success"),
            message: this.initTrain.trainNum + this.traduire("addSuccess"),
            type: "success",
          });
        } else {
          this.tableData[idx] = JSON.parse(JSON.stringify(this.initTrain));
          this.addTrainDialog = false;
          ElNotification({
            title: this.traduire("success"),
            message: this.initTrain.trainNum + this.traduire("modSuccess"),
            type: "success",
          });
        }
      },
    },
  };
</script>
<style lang="scss">
  .add-train-dialog {
    .edit-time-part {
      width: 100%;

      overflow: auto;
      white-space: nowrap;
      .time-box {
        box-sizing: border-box;
        margin-right: 8px;
        padding: 0 8px;
        height: 100px;
        min-width: 100px;
        border: 1px solid gray;
        display: inline-flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        border-radius: 8px;
        input {
          text-align: center;
          width: 100px;
        }
      }
    }
  }

  .type-column {
    display: flex;
    justify-content: center;
    align-items: center;
    .type-icon {
      margin: 0 4px;
      height: 20px;
      width: 20px;
    }
  }
</style>
