<template>
  <div class="inner-page">
    <!-- <h2>{{ traduire("station") }}</h2> -->
    <div class="container">
      <el-button
        type="primary"
        @click="openAddStationDialog"
        class="huge-btn"
        >{{
          stationsList.length > 0
            ? traduire("editStation")
            : traduire("addStation")
        }}</el-button
      >
    </div>
    <div class="stations-line">
      <div class="station" v-for="(s, index) in stationsList" :key="index">
        {{ s }}
      </div>
    </div>
    <el-button
      v-if="stationsList.length > 0"
      type="primary"
      @click="next"
      class="normal-btn"
      >{{ traduire("next") }}</el-button
    >
  </div>
  <!-- 编辑车站弹窗 -->
  <el-dialog
    v-model="addStationDialog"
    :title="traduire('editStation')"
    width="600px"
  >
    <el-input
      type="textarea"
      autosize
      v-model="stationsText"
      :placeholder="traduire('stationTip')"
      @change="generateStations"
    ></el-input>

    <template #footer>
      <span class="dialog-footer">
        <el-button
          type="primary"
          @click="addStationDialog = false"
          class="normal-btn"
          >{{ traduire("confirm") }}</el-button
        >
      </span>
    </template>
  </el-dialog>
</template>

<script>
  import { ElNotification } from "element-plus";
  import zhCn from "@/assets/i18n/zh-cn";
  export default {
    name: "KanBan",
    props: ["stations", "locale"],
    watch: {
      stations() {
        this.stationsList = this.stations;
      },
    },

    data() {
      return {
        initTrain: {
          trainNum: "",
          stops: {},
          type: "",
        },
        addStationDialog: false,
        addTrainDialog: false,
        stationsText: "",
        stationsList: [],
      };
    },

    methods: {
      /**
       * @description: 下一步
       * @param {*}
       */
      next() {
        this.$emit("next");
      },
      /**
       * @description: 翻译
       * @param {*} name
       */
      traduire(name) {
        return this.locale[name] !== undefined ? this.locale[name] : zhCn[name];
      },
      /**
       * @description: 添加/编辑车站触发
       * @param {*}
       */
      openAddStationDialog() {
        this.stationsText = "";
        this.stationsList.forEach((s) => {
          this.stationsText += s + " ";
        });
        this.addStationDialog = true;
      },
      /**
       * @description: 解析车站
       * @param {*}
       */
      generateStations() {
        this.stationsList = this.stationsText.split(" ");
        ElNotification({
          title: "成功",
          message: "车站修改成功！",
          type: "success",
        });
        this.$emit("station-changed", this.stationsList);
      },
    },
  };
</script>

<style lang="scss">
  .stations-line {
    display: flex;

    width: 100%;
    justify-content: center;
    align-items: center;
    .station {
      margin: 0 16px;
      border: 1px solid darkgray;
      border-radius: var(--theme-btn-radius);
      min-width: 100px;
      height: 50px;
      font-size: 18px;
      line-height: 50px;
      text-align: center;
      color: whitesmoke;
      background: gray;
    }
  }
</style>
