<template>
  <div>
    <el-container>
      <el-header
        ><el-image
          style="width: 50px; height: 50px"
          :src="require('/public/logo.png')"
        ></el-image>
        <span class="grand-title">{{ traduire("title") }}</span>
        <el-select v-model="selectedLang" @change="langChanged">
          <el-option
            v-for="l in langs"
            :key="l.name"
            :label="l.name"
            :value="l"
          />
        </el-select>
      </el-header>

      <el-main>
        <el-tabs tab-position="left" v-model="activeTab">
          <el-tab-pane :label="traduire('import')" name="import">
            <div class="container">
              <el-button class="huge-btn" type="primary" @click="chooseFile">{{
                traduire("choose")
              }}</el-button>
              <input
                type="file"
                id="fileInput"
                @change="read"
                style="display: none"
              />

              <el-button class="huge-btn" type="primary" @click="createNew">{{
                traduire("new")
              }}</el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane :label="traduire('basic')" name="basic">
            <BasicVue :config="config" :locale="locale" @submit="basicSubmit" />
          </el-tab-pane>
          <el-tab-pane :label="traduire('station')" name="station"
            ><KanBan
              :locale="locale"
              :stations="config.stations"
              @station-changed="stationChanged"
              @next="activeTab = 'table'"
            ></KanBan
          ></el-tab-pane>
          <el-tab-pane :label="traduire('table')" name="table">
            <TimeTable
              ref="tableShang"
              :locale="locale"
              :tableInit="config.tableShang"
              :title="traduire('aller')"
              :stations="config.stations"
            ></TimeTable>
            <el-divider></el-divider>
            <TimeTable
              :locale="locale"
              ref="tableXia"
              :tableInit="config.tableXia"
              :title="traduire('retour')"
              :stations="config.stations.slice().reverse()"
            ></TimeTable>
          </el-tab-pane>
          <el-tab-pane :label="traduire('export')" name="export">
            <div class="container">
              <el-button type="primary" @click="exportData" class="huge-btn">{{
                traduire("export")
              }}</el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane :label="traduire('about')" name="about">
            <div class="container">
              <FooterVue :locale="locale"></FooterVue>
            </div>
          </el-tab-pane> </el-tabs
      ></el-main>

      <!-- <el-footer><FooterVue></FooterVue></el-footer> -->
    </el-container>
  </div>
</template>

<script>
  import BasicVue from "./BasicVue.vue";
  import FooterVue from "./FooterVue.vue";
  import TimeTable from "./TimeTable.vue";
  import KanBan from "./KanBan.vue";
  import zhCn from "@/assets/i18n/zh-cn";
  import en from "@/assets/i18n/en";
  import fr from "@/assets/i18n/fr";
  import de from "@/assets/i18n/de";
  import ja from "@/assets/i18n/ja";

  export default {
    name: "MainPage",
    components: { BasicVue, TimeTable, KanBan, FooterVue },

    data() {
      return {
        fileName: "",
        locale: zhCn,
        langs: [
          { name: "中文", value: zhCn, code: "zh_cn" },
          { name: "EN", value: en, code: "en" },
          { name: "FR", value: fr, code: "fr" },
          { name: "DE", value: de, code: "de" },
          { name: "日本语", value: ja, code: "ja" },
        ],
        selectedLang: { name: "中文", value: zhCn, code: "zh_cn" },
        activeTab: "import",
        config: {
          stations: [],
          tableShang: [],
          tableXia: [],
          lingName: "",
          pos: 0,
          updateDate: "",
        },
      };
    },
    mounted: function () {
      console.log("input", this.$refs.inputBox);
    },
    methods: {
      chooseFile() {
        let inputHtml = document.getElementById("fileInput");
        inputHtml.click();
      },
      createNew() {
        this.config = {
          stations: [],
          tableShang: [],
          tableXia: [],
          lingName: "",
          pos: 0,
          updateDate: "",
        };
        this.activeTab = "basic";
      },
      traduire(name) {
        console.log("local", this.locale[name], zhCn[name]);
        return this.locale[name] !== undefined ? this.locale[name] : zhCn[name];
      },
      /**
       * @description: 切换语言
       * @param {*}
       */
      langChanged(lan) {
        this.locale = lan.value;
        this.$emit("change-lang", lan.code);
      },
      /**
       * @description: 导出数据
       * @param {*}
       */
      exportData() {
        this.config.tableShang = this.$refs.tableShang.tableData;
        this.config.tableXia = this.$refs.tableXia.tableData;

        if (
          (this.config.tableShang.length != 0 ||
            this.config.tableShang.length != 0) &&
          this.config.lineName != ""
        ) {
          const data = JSON.stringify(this.config);
          console.log(this.config);
          const blob = new Blob([data], { type: "text/plain" });
          const e = document.createEvent("MouseEvents"),
            a = document.createElement("a");
          a.download = this.config.lineName + "配置文件.json";
          a.href = window.URL.createObjectURL(blob);
          a.dataset.downloadurl = ["text/json", a.download, a.href].join(":");
          e.initEvent(
            "click",
            true,
            false,
            window,
            0,
            0,
            0,
            0,
            0,
            false,
            false,
            false,
            false,
            0,
            null
          );
          a.dispatchEvent(e);
        }
      },
      /**
       * @description: 车站列表更改
       * @param {*} list 更改后的列表
       */
      stationChanged(list) {
        this.config.stations = list;
      },

      /**
       * @description: 基础信息submit
       * @param {*} info 修改后的信息
       */
      basicSubmit(info) {
        this.config.lingName = info.lineName;
        this.config.pos = info.pos;
        this.config.updateDate = info.updateDate;
        this.activeTab = "station";
        console.log("config", this.config);
      },
      /**
       * @description: 读取文件
       * @param {*} e
       */
      read(e) {
        const reader = new FileReader();
        let that = this;
        reader.onload = function fileReadCompleted() {
          var config = JSON.parse(reader.result);
          console.log(config);
          that.config.tableShang = config.tableShang;
          that.config.tableXia = config.tableXia;
          that.config.stations = config.stations;
          that.config = config;
          that.$forceUpdate();
        };
        reader.readAsText(e.target.files[0]);
        this.activeTab = "basic";
      },
    },
  };
</script>

